import Image from 'next/image';
import React from 'react';
import close from '/public/static/img/close.svg';

import styles from './Search.module.scss';
import { useRouter } from 'next/router';
import routes from 'helpers/routes';

type SearchTypes = {
	searchRef: React.RefObject<HTMLDivElement>;
	searchCloseButtonRef?: React.RefObject<HTMLDivElement>;
	closeSearch?: () => void;
};

const Search = ({ closeSearch, searchRef, searchCloseButtonRef }: SearchTypes) => {
	const [searchValue, setSearchValue] = React.useState<string>('');

	const router = useRouter();

	const onChangeSearchValue = (event: React.ChangeEvent<HTMLInputElement>) => {
		const { target } = event;

		setSearchValue(target.value);
	};

	const blurSearchHandler = (event: React.FormEvent<HTMLInputElement>) => {
		const ua = navigator.userAgent.toLowerCase();
		const isAndroid = ua.indexOf('android') > -1; //&& ua.indexOf("mobile");
		if (isAndroid && event.currentTarget.value !== '') {
			router.push(routes.catalog + 'search?value=' + searchValue);
		}
	};

	const pressEnterHandler = (event: React.KeyboardEvent) => {
		const { code } = event;
		if (!searchValue.replace(/\s/g, '')) return;
		if (code !== 'Enter') return;
		router.push(routes.catalog + 'search?value=' + searchValue);
		event.preventDefault();
		event.stopPropagation();
	};

	const keyPressHandler = (event: React.KeyboardEvent<HTMLInputElement>) => {
		if (/Android/i.test(navigator.userAgent)) event.currentTarget.blur();
	};

	return (
		<div ref={searchRef} className={styles.search}>
			<div className={styles.input}>
				<input
					placeholder="Поиск"
					onKeyPress={keyPressHandler}
					onBlur={blurSearchHandler}
					onKeyDown={pressEnterHandler}
					onChange={onChangeSearchValue}
					value={searchValue}
				/>
			</div>
			<div className={styles.closeBtn} ref={searchCloseButtonRef}>
				<Image
					alt="X"
					onClick={closeSearch}
					src={close}
					width={24}
					height={24}
					style={{
						maxWidth: '100%',
						height: 'auto',
					}}
				/>
			</div>
		</div>
	);
};

export default React.memo(Search);
